@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&family=Roboto+Slab:wght@300;400;700&display=swap");

.header-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 1rem 6.25rem;
  border-bottom: solid 1px #e8e8e8;
  white-space: nowrap;
}

.header-container .title {
  width: 15rem;
  height: 2.313rem;
  margin: 0.375rem 2.938rem 0.125rem 0;
  font-family: Montserrat, Arial, sans-serif;
  font-size: 1.875rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #0290ff;
  white-space: nowrap;
}

.header-container .links-container {
  display: flex;
}

.header-container .links-container .links {
  width: 3.688rem;
  height: 1rem;
  margin: 1.125rem 2.563rem 0.688rem 0;
  font-family: Montserrat, Arial, sans-serif;
  font-size: 0.813rem;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 1.9px;
  color: #121212;
}
